import React,{Component} from 'react'
import {connect} from 'react-redux'
import Head from 'next/head'


import {Layout, Menu, Breadcrumb, Row, Col, Pagination, Input,Tabs,Table,List, Avatar, Icon} from 'antd';

import Header from '../../components/Header';


const TabPane = Tabs.TabPane;
const {Content} = Layout;
const Search = Input.Search;
class Admin extends Component {
  constructor() {
    super()
    this.state = {
      pageNum: 10,
      currentPage: 1,
      inputVal: ''
    }
  }

  componentWillMount() {
  }

  itemRender(current, type, originalElement) {
    if (type === 'prev') {
      return <a>Previous</a>;
    } else if (type === 'next') {
      return <a>Next</a>;
    }
    return originalElement;
  }

  onChange(page, pageSize) {

  }

  onSearch(val) {
    this.setState({
      inputVal: val,
      currentPage: 1
    })
  }
  handleDelComment(id){
  }
  handleDelArticle(id){

  }
  render () {

    function onChange(pagination, filters, sorter) {
      console.log('params', pagination, filters, sorter);
    }

    function onClick(pagination, filters, sorter) {
      console.log('onClick', pagination, filters, sorter);
    }

    function callback(key) {
      console.log(key);
    }

    return (
      <div>
        <Head>
          <meta name='viewport' content='width=device-width, initial-scale=1' />
          <meta charSet='utf-8' />
          <link rel='stylesheet' href='/_next/static/style.css' />
          <title>admin</title>
        </Head>
        <Header/>
        <Layout>
          <Content style={{padding: '0 50px'}}>
            <Search placeholder="input search text" onSearch={this.onSearch.bind(this)} enterButton="Search"
                    size="large"/>
            <div style={{background: '#fff', padding: 24, minHeight: 380}}>
              <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="文章管理" key="1">
                  <Table
                    bordered={true}
                    columns={columns}
                    dataSource={data}
                    pagination={false}
                    onChange={onChange}
                    onRow={(record) => {
                      return {
                        onClick: () => {
                          console.log(record)
                        },       // 点击行
                        onMouseEnter: () => {
                        },  // 鼠标移入行
                      };
                    }}
                  />
                  <Pagination

                    defaultCurrent={this.state.currentPage}
                    total={100} itemRender={this.itemRender.bind(this)} onChange={this.onChange.bind(this)}/>
                </TabPane>
                <TabPane tab="留言管理" key="2">

                  <Table
                    bordered={true}
                    columns={CommentsColumns}
                    dataSource={CommentsData}
                    pagination={false}
                  />
                </TabPane>
                <TabPane tab="浏览记录" key="3">Content of Tab Pane 3</TabPane>
              </Tabs>
            </div>
          </Content>
        </Layout>
      </div>
    )
  }
}

export default Admin


